body{--black-raw: 0, 0, 0;--white-raw: 255, 255, 255;--accent_color-raw: 86, 111, 207;--danger_color-raw: 221, 0, 0;--black: rgb(var(--black-raw));--white: rgb(var(--white-raw));--accent_color: rgb(var(--accent_color-raw));--danger_color: rgb(var(--danger_color-raw));--black_2: rgba(var(--black-raw), 0.02);--black_4: rgba(var(--black-raw), 0.04);--black_5: rgba(var(--black-raw), 0.05);--black_10: rgba(var(--black-raw), 0.1);--black_15: rgba(var(--black-raw), 0.15);--black_20: rgba(var(--black-raw), 0.20);--black_40: rgba(var(--black-raw), 0.40)}.root{height:100%;box-sizing:border-box;background-color:#f0f0f0}.tab-panel{display:flex;padding:12px 16px;background-color:var(--sand_0);justify-content:space-between}.tab-panel_newUI{padding:8px}.grid{flex:1}.grid__item{padding:10px;justify-content:space-between;box-sizing:border-box;box-shadow:-0.5px -0.5px 0 .5px #ccc}.container{padding:2em;width:90%;height:600px;margin-left:auto;margin-right:auto}.app-loader{width:100%;height:100%;display:flex;align-items:center;justify-content:center}.ant-picker-cell{padding:3px 0 !important;border:none !important}.offscreen-lsf{bottom:100%;right:100%;opacity:0;position:absolute;width:100vw;height:100vh;background-color:var(--sand_0);z-index:100000}.offscreen-lsf.visible{bottom:auto;right:auto;top:0;left:0;opacity:1}.fill-container{flex:1;display:flex;align-items:center;justify-content:center}.horizontal-shadow::after{top:100%;width:100%;left:0;content:"";position:absolute;height:4px;background:linear-gradient(to bottom, var(--black_15), transparent)}.crash{width:100vw;height:100vh;display:flex;align-items:center;justify-content:center;flex-direction:column}.crash__header{font-size:22px;font-weight:500}.crash__description{margin-top:32px;font-size:16px}