.annotators{display:grid;grid-auto-flow:column;grid-column-gap:4px}.annotators__item{z-index:1;width:28px;height:28px;border-radius:100%;position:relative}.annotators__item:hover{z-index:10}.userpic-badge{width:10px;height:10px;color:var(--sand_0);display:flex;align-items:center;justify-content:center;border-radius:2px;box-shadow:0 0 0 2px var(--sand_0)}.userpic-badge_accepted{background-color:#64bf00}.userpic-badge_rejected{background-color:#d00}.userpic-badge_fixed{background-color:#fa8c16}