.filters{background-color:#fff;position:relative}.filters:not(.filters_sidebar){padding-top:10px;min-width:400px;border-radius:2px;box-shadow:0 3px 6px -4px rgba(0,0,0,.12),0 6px 16px 0 rgba(0,0,0,.08),0 9px 28px 8px var(--black_5)}.filters_sidebar{width:100%}.filters_sidebar .filter-line{padding-right:10px;padding-left:10px;align-items:stretch}.filters_sidebar .filter-line .ant-divider{margin:0;height:24px}.filters_sidebar .filter-line__field{width:100%}.filters_sidebar .filter-line__settings{flex-direction:column}.filters_sidebar .filter-line__remove{height:56px}.filters_sidebar .filter-line__group{flex:1;padding:2px 0;width:100%}.filters__actions{display:flex;margin-top:10px;padding:0 10px 10px;justify-content:space-between}.filters__empty{padding:0 10px;font-size:14px;color:#585858}.filters__list{padding:0 10px}.filters__list_withFilters{display:grid;grid-template-columns:65px min-content min-content 1fr 24px;grid-gap:3px 4px}.filters_sidebar .filters__list_withFilters{grid-template-columns:1fr 1fr 24px;grid-row-gap:5px;grid-auto-flow:row;align-items:center}.filter-data-tag{margin:1px;font-size:12px;padding:3px 5px;display:inline-flex;align-items:center}