.columns__selector{position:absolute;width:100%;overflow:hidden;display:flex;justify-content:flex-end;top:0}.columns__selector__button-new.columns__selector__button-new{z-index:1000;height:28px;width:28px;background-color:var(--white);box-shadow:0 2px 5px 0 var(--black_20);margin:6.5px 20px;border-radius:4px}.columns__selector__button-new.columns__selector__button-new:hover{border:none;filter:none;background:#ebf3fc}.columns__selector__button-new.columns__selector__button-new:active{background:linear-gradient(0deg, var(--black_2), var(--black_2)),#fff;box-shadow:0 0 5px 0 var(--black_20)}.table{width:100%;height:100%;display:flex;flex-direction:column;overflow:auto;background-color:var(--white)}.table_fit{width:max-content}.table__sticky-header{width:100%}.table__auto-size,.table__virtual{width:auto}.table_fit .table__auto-size,.table_fit .table__virtual{width:fit-content}.table__virtual{max-width:100vw;min-width:100vw}.table__virtual>div{min-width:fit-content}.table__row-wrapper{min-width:fit-content;position:relative;cursor:pointer;background-color:var(--sand_0)}.table__row-wrapper_disabled,.table__row-wrapper_loading{opacity:.6;pointer-events:none}.table__row-wrapper_even:not(.table__row_wrapper_selected){background-color:var(--sand_100)}.table__row-wrapper_selected{background-color:var(--grape_0)}.table__row-wrapper:not(.table__row_wrapper_selected):hover{background-color:var(--grape_0)}.table__row-wrapper_highlighted:not(.table__row_wrapper_selected)::after{top:0;left:0;width:100%;height:100%;content:"";z-index:50;position:absolute;pointer-events:none;box-shadow:0 0 0 1px rgba(var(--accent_color-raw), 0.8) inset}.table__cell{flex:150 0 auto;width:fit-content;min-width:min-content;max-width:min-content;padding:10px;box-sizing:border-box;display:flex;align-items:center;position:relative;overflow:hidden;word-break:break-word}.table__cell-content{flex:0;display:flex;white-space:nowrap;align-items:center}.table__cell-content_disabled{opacity:.6;pointer-events:none}