.form-input,.form-select,.textarea{height:40px;min-height:40px;background:#fafafa;font-size:16px;line-height:22px;border:1px solid var(--sand_300);box-sizing:border-box;border-radius:5px;padding:0 16px;transition:box-shadow 80ms ease}.form-input_newUI,.form-select_newUI,.textarea_newUI{border-color:rgba(137,128,152,.16)}.form-input_ghost,.form-select_ghost,.textarea_ghost{border:none;padding:0;background-color:rgba(0,0,0,0);outline:none}.form-input_size_small,.form-select_size_small,.textarea_size_small{height:24px;font-size:12px;line-height:18px;min-height:24px;padding:0 8px}input.form-input[type=radio]{width:16px;height:16px;min-height:0}input.form-input[type=range]{padding:0}.textarea{padding:12px 16px;min-height:50px}