.dropdown{--menu-animation-duration: 0.15s;--menu-animation-curve: cubic-bezier(0.21, 1.04, 0.68, 1);--menu-animation-start: -10px;top:calc(100% + 1px);z-index:500;display:none;position:absolute;box-sizing:border-box;background-color:var(--sand_0);box-shadow:0 5px 20px var(--black_20);will-change:transform,opacity}.dropdown_align_left{left:-20px}.dropdown_align_right{right:-20px}.dropdown__trigger{position:relative}.dropdown.before-appear,.dropdown.before-disappear{transition-property:opacity,transform;transition-duration:var(--menu-animation-duration);transition-iming-function:var(--menu-animation-curve)}.dropdown.before-appear{opacity:0;display:flex;transform:translate3d(0, var(--menu-animation-start), 0)}.dropdown.appear{opacity:1;transform:translate3d(0, 0, 0)}.dropdown.visible{opacity:1;display:flex}.dropdown.before-disappear{opacity:1;display:flex;transform:translate3d(0, 0, 0)}.dropdown.disappear{opacity:0;transform:translate3d(0, var(--menu-animation-start), 0);z-index:-1 !important}