.range{display:inline-flex;align-items:center;user-select:none}.range_align_horizontal{min-width:120px;height:28px;flex-direction:row}.range_align_vertical{min-height:120px;width:28px;flex-direction:column}.range_with-icon .range_align_horizontal{padding:0 5px}.range_with-icon .range_align_vertical{padding:5px 0}.range__body{position:relative}.range_align_horizontal .range__body{height:4px;width:100%}.range_align_vertical .range__body{height:100%;width:4px}.range_align_horizontal .range__body_with-icon{margin:0 5px}.range_align_vertical .range__body_with-icon{margin:5px 0}.range__line{background:linear-gradient(0deg, var(--black_5), var(--black_5)),#fff;box-shadow:inset 0px 1px 0px var(--black_5),inset 0px 0px 0px 1px var(--black_5);border-radius:8px}.range_align_horizontal .range__line{height:4px;width:100%}.range_align_vertical .range__line{width:4px;height:100%}.range__indicator{border-radius:8px;position:absolute;background-color:rgba(var(--accent_color-raw), 0.5)}.range_align_horizontal .range__indicator{top:0;height:4px}.range_align_vertical .range__indicator{left:0;width:4px}.range__range-handle{width:0;height:0;position:absolute}.range__range-handle::before{content:"";position:absolute;background:var(--grape_500);box-shadow:0px 5px 10px rgba(0,153,255,.3),inset 0px -1px 0px var(--black_10);border-radius:2px;transform:translate3d(-50%, -50%, 0)}.range__range-handle::after{content:"";width:25px;height:25px;position:absolute;transform:translate3d(-50%, -50%, 0)}.range_align_horizontal .range__range-handle{top:50%;cursor:col-resize}.range_align_horizontal .range__range-handle::before{width:10px;height:16px}.range_align_vertical .range__range-handle{left:50%;cursor:row-resize}.range_align_vertical .range__range-handle::before{width:16px;height:10px}.range__icon{width:28px;height:28px;display:flex;align-items:center;justify-content:center}