.toggle{width:42px;height:24px;display:block;min-width:42px;max-height:24px;border-radius:12px;position:relative;cursor:pointer;transition:box-shadow 80ms ease;background:linear-gradient(0deg, var(--black_5), var(--black_5)),#fff;box-shadow:inset 0px 1px 0px var(--black_5),inset 0px 0px 0px 1px var(--black_5)}.toggle__input{top:0;left:0;margin:0;opacity:0;padding:0;width:100%;height:100%;position:absolute}.toggle__indicator{height:24px;width:24px;display:flex;align-items:center;justify-content:center;transition:all 120ms ease}.toggle__indicator::before{content:"";width:10px;height:10px;border-radius:100%;transition:all 120ms ease;background:rgba(var(--black-raw), 0.12);box-shadow:0px 5px 10px var(--black_15),inset 0px -1px 0px var(--black_10),inset 0px 0px 0px 1px var(--black_5)}.toggle_checked .toggle__indicator{margin-left:18px}.toggle_checked .toggle__indicator::before{width:16px;height:16px;background:var(--accent_color);box-shadow:0px 5px 10px rgba(var(--accent_color-raw), 0.3),inset 0px -1px 0px var(--black_10)}.toggle_disabled{box-shadow:none;pointer-events:none;background:rgba(0,0,0,.08)}.toggle_disabled .toggle__indicator{transition:none}.toggle_disabled .toggle__indicator::before,.toggle_disabled.toggle_checked .toggle__indicator::before{box-shadow:none;transition:none;background-color:rgba(0,0,0,.12)}