.radio-group{--radius: 8px;--height: 40px;--padding: 4px;--font-size: 16px;--button-padding: 0 10px;--button-checked-shadow: 0px 1px 0px var(--black_10), 0px 0px 0px 1px var(--black_2), 0px 5px 10px var(--black_15);height:var(--height);border-radius:var(--radius);padding:var(--padding);background:linear-gradient(0deg, var(--black_5), var(--black_5)),#fff;box-shadow:inset 0px 1px 0px var(--black_5),inset 0px 0px 0px 1px var(--black_5)}.radio-group__buttons{height:calc(var(--height) - var(--padding)*2);display:grid;grid-auto-columns:1fr;grid-auto-flow:column}.radio-group__button{display:flex;opacity:.6;padding:var(--button-padding);font-weight:500;position:relative;text-align:center;align-items:center;justify-content:center;font-size:var(--font-size);border-radius:calc(var(--radius) - var(--padding));height:calc(var(--height) - var(--padding)*2);cursor:pointer}.radio-group__button_checked{opacity:1;background-color:var(--white);box-shadow:var(--button-checked-shadow)}.radio-group__button_disabled{opacity:.3;cursor:not-allowed}.radio-group__input{top:0;left:0;opacity:0;width:100%;height:100%;position:absolute}.radio-group_size_medium{--height: 32px;--radius: 8px;--font-size: 14px}.radio-group_size_small{--height: 24px;--radius: 4px;--padding: 2px;--font-size: 12px;--button-padding: 0 5px;--button-checked-shadow: 0px 1px 0px var(--black_10), 0px 0px 0px 1px var(--black_2), 0px 2px 4px var(--black_15)}.radio-group_newUI{--button-checked-shadow: none;--button-padding: 4px 8px;box-shadow:none;background:rgba(240,238,242,.08);border:1px solid rgba(65,60,74,.16)}.radio-group_newUI .radio-group__buttons{gap:8px}.radio-group_newUI .radio-group__button{--button-border-width: 1px;opacity:1;border:var(--button-border-width) solid rgba(0,0,0,0);height:calc(var(--height) - var(--padding)*2 - var(--button-border-width)*2);cursor:pointer}.radio-group_newUI .radio-group__button .radio-group__input{cursor:pointer}.radio-group_newUI .radio-group__button_checked{color:var(--grape_500);background:var(--grape_0);border-color:var(--grape_500);cursor:default}.radio-group_newUI .radio-group__button_checked .radio-group__input{cursor:default}.radio-group_newUI .radio-group__button:hover{background:linear-gradient(0deg, rgba(9, 109, 217, 0.05), rgba(9, 109, 217, 0.05)),#fafafa}