.form-select{position:relative}.form-select__list{top:0;left:0;width:100%;height:100%;padding:0 16px;margin:0;border:none;border-radius:3px;position:absolute;background-color:rgba(0,0,0,0);transition:box-shadow 80ms ease;font-size:inherit;border-right:10px solid rgba(0,0,0,0)}.form-select_size_small .form-select__list{padding:0 8px}.form-select_ghost{margin-left:-10px;border:1px solid rgba(0,0,0,0)}.form-select_ghost .form-select__list{padding:0 5px}.form-select_ghost:hover{border:1px solid var(--sand_300)}