.pagination{--button-color: var(--accent_color);--border-radius: 5px;--height: 40px;--font-size: 16px;--line-height: 19px;height:var(--height);display:inline-flex;align-items:center}.pagination_size_small{--height: 24px;--border-radius: 3px;--font-size: 12px;--line-height: 15px}.pagination_disabled{--button-color: #ccc;background-color:#efefef;pointer-events:none}.pagination__label{padding-right:16px;color:var(--black_40);font-size:var(--font-size);line-height:var(--line-height)}.pagination_size_small .pagination__label{padding-right:8px}.pagination__navigation{display:flex;height:100%;align-items:center;border-radius:var(--border-radius, 5px);background:var(--white);box-shadow:inset 0px -1px 0px var(--black_10),inset 0px 0px 0px 1px var(--black_15)}.pagination_waiting .pagination__navigation{pointer-events:none;animation:button-waiting 1s linear infinite;background-image:repeating-linear-gradient(-63.43deg, rgba(var(--white-raw), 0.2) 1px, #efefef 2px, #efefef 6px, rgba(var(--white-raw), 0.2) 7px, rgba(var(--white-raw), 0.2) 12px)}.pagination__divider{width:1px;height:30px;background-color:var(--black_10)}.pagination_size_small .pagination__divider{height:20px}.pagination__btn{width:37px;height:100%;display:flex;align-items:center;justify-content:center;user-select:none;cursor:pointer}.pagination_waiting .pagination__btn{opacity:.5}.pagination_size_small .pagination__btn{width:24px}.pagination__btn_disabled{pointer-events:none;--button-color: #ccc}.pagination__btn_arrow-left,.pagination__btn_arrow-right{position:relative}.pagination__btn_arrow-left:before,.pagination__btn_arrow-left:after,.pagination__btn_arrow-right:before,.pagination__btn_arrow-right:after{content:"";display:block;position:absolute;left:50%;top:50%;width:9px;height:9px;border:2px solid var(--button-color);border-bottom:none;border-right:none}.pagination__btn_arrow-left:after,.pagination__btn_arrow-right:after{display:none}.pagination_size_small .pagination__btn_arrow-left::before,.pagination_size_small .pagination__btn_arrow-left::after,.pagination_size_small .pagination__btn_arrow-right::before,.pagination_size_small .pagination__btn_arrow-right::after{width:5px;height:5px}.pagination__btn_arrow-left:before,.pagination__btn_arrow-left:after{transform:translate3d(-50%, -50%, 0) rotate(-45deg)}.pagination__btn_arrow-right:before,.pagination__btn_arrow-right:after{transform:translate3d(-50%, -50%, 0) rotate(135deg)}.pagination__btn_arrow-left-double:after{display:block;margin-left:7px}.pagination__btn_arrow-right-double:after{display:block;margin-left:-7px}.pagination__input{width:100px;height:calc(100% - 2px);text-align:center;display:flex;align-items:center;justify-content:center;border:1px solid #d9d9d9;border-top:none;border-bottom:none;background:#fafafa;margin:1px 0}.pagination_waiting .pagination__input{opacity:.5}.pagination_size_small .pagination__input{width:70px}.pagination__input input{width:100%;height:calc(100% - 2px);border:none;margin:1px 0;padding:0;outline:none;text-align:center;background:none;font-weight:500;font-size:var(--font-size);line-height:var(--line-height)}.pagination__page-indicator{flex:1;user-select:none;font-weight:500;font-size:var(--font-size);line-height:var(--line-height)}.pagination__page-indicator span{font-weight:normal;opacity:.4}.pagination__page-size{min-width:170px;padding-left:16px}.pagination_size_small .pagination__page-size{padding-left:8px;min-width:130px}