.modal{--transition-duration: 100ms;top:0;left:0;width:100%;height:100%;z-index:10000;display:flex;position:absolute;align-items:center;justify-content:center;background-color:rgba(var(--black-raw), 0.3);will-change:opacity}.modal__wrapper{width:100%;max-height:100%;padding:40px 0;overflow:auto;box-sizing:border-box;will-change:transform}.modal__content{width:400px;min-width:400px;min-height:100px;margin:0 auto;background-color:var(--white);border-radius:5px;box-shadow:0 10px 30px rgba(var(--black-raw), 0.3)}.modal__header{display:flex;min-height:40px;align-items:center;justify-content:space-between;padding:16px 32px;box-sizing:content-box}.modal__header_divided{border-bottom:1px solid var(--black_5)}.modal__title{margin:0;margin-right:auto;font-size:20px}.modal__body{padding:0 40px 32px}.modal__body_bare{padding:0;height:100%;display:flex;flex-direction:column}.modal__footer{padding:16px 32px;background:rgba(0,0,0,.03);box-shadow:inset 0px 1px 0px var(--black_5);text-align:center;font-size:14px;line-height:22px}.modal_fullscreen .modal__content{width:calc(100vw - 80px);height:calc(100vh - 80px)}.modal_visible{opacity:0;transition:opacity var(--transition-duration) ease}.modal_visible .modal__wrapper{transform:scale(1.05);transition:transform var(--transition-duration) ease}.modal.visible{opacity:1}.modal.visible .modal__wrapper{transform:none}.modal.before-appear{opacity:0}.modal.before-appear .modal__wrapper{transform:scale(1.05)}.modal.appear{opacity:1}.modal.appear .modal__wrapper{transform:scale(1)}.modal.before-disappear{opacity:1}.modal.before-disappear .modal__wrapper{transform:scale(1)}.modal.disappear{opacity:0}.modal.disappear .modal__wrapper{transform:scale(1.05)}