.resizer{position:relative}.resizer button:focus{outline:none;box-shadow:none}.resizer__content{margin-left:-4px;padding-inline:4px}.resizer__handle{top:0;left:100%;height:100%;padding:0 5px;position:absolute;cursor:col-resize;z-index:100}.resizer__handle::before{top:0;left:50%;bottom:0;width:1px;content:"";z-index:5;display:block;background:#bdbdbd;position:absolute}.resizer__handle_quickview::before{left:0}.resizer__handle:hover::before,.resizer__handle_resizing::before{top:0;bottom:0;background-color:var(--accent_color);box-shadow:0 0 0 1px var(--accent_color)}