.userpic{width:28px;height:28px;display:flex;font-size:12px;position:relative;align-items:center;border-radius:50%;justify-content:center;background:rgba(0,0,0,.08);box-shadow:0px 0px 0px 1px var(--black_10)}.userpic img{opacity:0;width:100%;height:100%;font-size:12px;line-height:22px;object-fit:cover;position:absolute;border-radius:100%}.userpic__username{display:block;line-height:22px;font-weight:bold;opacity:.6}.userpic__badge{position:absolute}.userpic__badge_topLeft{top:0;left:0}.userpic__badge_topRight{top:0;right:0}.userpic__badge_bottomLeft{bottom:0;left:0}.userpic__badge_bottomRight{bottom:0;right:0}.userpic_faded{background:rgba(0,0,0,.03);box-shadow:inset 0px 0px 0px 1px var(--black_10)}.userpic_faded .userpic__username{opacity:.2}.userpic_faded img{opacity:.3}