.menu{flex:1;margin:0;padding:8px;display:flex;flex-direction:column;list-style-type:none}.menu ul,.menu li{display:block;list-style-type:none}.menu li{margin:.5rem}.menu li+li{margin-top:0}.menu__item{height:32px;display:flex;cursor:pointer;padding:0 12px;border-radius:3px;align-items:center;box-sizing:border-box;color:var(--sand_600);font-size:14px;white-space:nowrap;user-select:none}.menu__item-icon{display:block;margin-right:10px;object-fit:contain;opacity:.5;display:flex;align-items:center;justify-content:center}.menu__item:not(.menu__item_look_danger):hover{color:var(--grape_700);background-color:var(--grape_0)}.menu__item_active{color:var(--grape_700);background-color:var(--grape_0)}.menu__item_active:not(.sidebar__pin):not(.menu__item_clickable){pointer-events:none}.menu__item:hover .menu__item-icon,.menu__item_active .menu__item-icon{opacity:1}.menu__item_look_danger{color:var(--danger_color)}.menu__item_look_danger:hover{background-color:var(--red_1)}.menu__spacer{flex:1}.menu__divider{height:1px;margin:8px 0;background-color:var(--black_10)}.menu_size_compact .menu__item{height:32px;font-size:16px}.menu_size_small .menu__item{height:24px;font-size:14px;padding:0 10px}.menu_collapsed{padding:0}.menu-group__title{padding:4px 10px;font-size:14px;color:rgba(var(--black-raw), 0.6)}.menu-group__list{padding:0;margin-left:10px;list-style-type:none}