.select{height:40px;background:#fafafa;font-size:16px;line-height:22px;border:1px solid var(--sand_300);box-sizing:border-box;border-radius:5px;cursor:pointer}.select_size_compact{height:32px}.select_size_small{height:24px;font-size:12px}.select__list{width:max-content}.select__selected{width:100%;padding:0 7px;min-width:60px;display:flex;font-weight:500;align-items:center}.select__value{width:max-content;min-width:30px;flex:1;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.select__option{cursor:pointer;padding:4px 16px}.select__option:hover,.select__option_focused{background-color:#f7f7f7}.select__option_selected{color:#fff;background-color:var(--accent_color)}.select__option_selected:hover,.select__option_selected.select__option_focused{background-color:hsl(var(--accent_color), 10%)}.select__icon{width:16px;height:16px;margin-left:5px;position:relative}.select__icon::before,.select__icon::after{width:6px;height:6px;display:block;position:absolute;content:"";border:1px solid var(--accent_color);border-right:none;border-bottom:none}.select__icon::before{top:0;left:50%;transform:translate(-50%, 50%) rotate(45deg)}.select__icon::after{bottom:0;left:50%;transform:translate(-50%, -50%) rotate(-135deg)}.select__optgroup-list{width:max-content}.select__optgroup-label{padding:4px 16px;color:rgba(var(--black-raw), 0.6)}.select__optgroup-list .select__option{padding-left:24px}.select_disabled{pointer-events:none}.select:focus{outline:none;box-shadow:0px 0px 0px 6px rgba(var(--accent_color-raw), 0.2),inset 0px -1px 0px var(--black_10),inset 0px 0px 0px 1px var(--black_15),inset 0px 0px 0px 1px rgba(var(--accent_color-raw), 0.2);border-color:rgba(var(--accent_color-raw), 0.2)}