.table-head{display:flex;width:100%;height:50px;z-index:150;font-weight:500;position:sticky;overflow:visible;background-color:var(--white);min-width:fit-content;font-size:14px;border-bottom:1px solid #d8d5dd}.table-head__extra{flex:1;display:flex;padding-right:14px;align-items:center;justify-content:flex-end}.table-head__column-extra{margin-left:10px;display:flex;align-items:center}.table-head__draggable{--translatex: 0;--translatey: 0;transform:translate(var(--translatex), var(--translatey));cursor:move}