.form{width:100%;display:block}.form__row{display:grid;justify-items:stretch;justify-content:space-between;grid-template-columns:repeat(var(--column-count, 5), 1fr);grid-column-gap:16px;grid-row-gap:var(--row-gap, 16px)}.form__row:not(:first-child){margin-top:12px}.form__row_spread .label{width:100%}.form__submit{display:flex;margin-top:32px;align-items:center;justify-content:space-between}.form__submit+.inline-error{margin-top:32px}.form__submit_size_small{margin-top:16px}.form__info{display:flex;align-items:center;color:#d00;font-size:14px;line-height:22px}.form__info_valid{color:var(--black_40)}.form__column{display:grid;grid-auto-flow:column;align-items:flex-start}.form-input:not(.form-input_ghost,.textarea_ghost,.counter_ghost,.form-select__list_ghost):focus,.form-input_focused,.textarea:not(.form-input_ghost,.textarea_ghost,.counter_ghost,.form-select__list_ghost):focus,.textarea_focused,.counter:not(.form-input_ghost,.textarea_ghost,.counter_ghost,.form-select__list_ghost):focus,.counter_focused,.form-select__list:not(.form-input_ghost,.textarea_ghost,.counter_ghost,.form-select__list_ghost):focus,.form-select__list_focused{outline:none;box-shadow:0px 0px 0px 6px rgba(var(--accent_color-raw), 0.2),inset 0px -1px 0px var(--black_10),inset 0px 0px 0px 1px var(--black_15),inset 0px 0px 0px 1px rgba(var(--accent_color-raw), 0.2);border-color:rgba(var(--accent_color-raw), 0.2)}.form-indicator{font-weight:500}.form-indicator__item_type_success{color:#59aa05}.form-indicator__item_type_fail{color:var(--danger_color)}